#wiki-surf-frame {
  border: 0.25rem solid #450bb6;
  box-sizing: border-box;
  height: calc(100vh - 18.5rem);
  margin-top: 2rem;
  min-height: 30rem;
  position: relative;
  width: 100%;
}

.wiki-button {
  display: block;
  margin-right: 0.5rem;
  width: 20rem;
}

.wiki-controls {
  display: flex;
  justify-content: center;
  align-items: center;
}

.wiki-intro {
  font-size: 1.25rem;
  margin: 4rem auto;
  width: 40rem;
}

.wiki-loader {
  width: 100%;
  margin-top: 8rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

.wiki-dropdown {
  margin-left: 0.5rem;
  width: 20rem;
}

@media (max-width: 1200px) {
  #wiki-surf-frame {
    height: calc(100vh - 16.5rem);
  }
}

@media (max-width: 768px) {
  #wiki-surf-frame {
    margin-top: 1rem;
    height: calc(100vh - 7rem);
  }

  .wiki-button {
    width: 48%;
  }

  .wiki-dropdown {
    width: 48%;
  }

  .wiki-intro {
    width: 100%;
  }
}

@media (max-width: 480px) {
  .wiki-button {
    margin: 0;
    width: 100%;
  }

  .wiki-controls {
    display: flex;
    flex-direction: column-reverse;
  }

  .wiki-dropdown {
    margin: 0.75rem 0;
    width: 100%;
  }
}
