/**************************************************
 * MODAL controls
*/

#modal {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  transition: 1s;
}

.modal-hide {
  display: none;
}

.modal-show {
  display: block;
}

/**************************************************
* COMMON
*/

.modal-btn {
  border-radius: 50%;
  height: 2.25rem;
  opacity: 0.6;
  position: absolute;
  transition: 0.1s;
  user-select: none;
  width: 2.25rem;
  z-index: 1;
}

.modal-btn:hover {
  cursor: pointer;
  opacity: 1;
  transform: scale(1.08);
}

.modal-btn:active {
  transform: scale(0.95);
}

.modal-btn:disabled {
  cursor: default;
  opacity: 0.08;
  transition: none;
  transform: none;
}

.modal-close {
  height: 1.5rem;
  right: 1rem;
  top: 1rem;
  width: 1.5rem;
}

.modal-fullscreen-btn {
  height: 1.5rem;
  right: 3.5rem;
  top: 1rem;
  width: 1.5rem;
}

.modal-shadow {
  height: 100%;
  left: 0;
  opacity: 0.75;
  position: fixed;
  top: 0;
  width: 100%;
  transition: 0.6s;
}

.modal-centered {
  box-sizing: border-box;
  padding: 0.5rem 0.25rem;
  max-height: 100%;
  left: 50%;
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
  max-width: 100%;
  pointer-events: visible;
}

/**************************************************
* SPECIFIC
*/

.shadow-dark {
  background-color: #000000;
}

.shadow-light {
  background-color: #f3f3f3;
}

.go-left {
  left: 0.25rem;
  top: 48%;
  transition: 0.8s;
}

.go-right {
  right: 0.25rem;
  top: 48%;
  transition: 0.8s;
}

@media (min-width: 769px) {
  .go-left {
    left: 0.75rem;
  }

  .go-right {
    right: 0.75rem;
  }
}

@media (min-width: 1201px) {
  .go-left {
    height: 3.5rem;
    width: 3.5rem;
    left: 4%;
  }

  .go-right {
    height: 3.5rem;
    width: 3.5rem;
    right: 4%;
  }
}

/**************************************************
* FULL SCREEN
*/

.fullscreen .modal-shadow {
  background-color: #000000;
  opacity: 1;
}

.fullscreen .modal-close {
  display: none;
}

.fullscreen .modal-fullscreen-btn {
  display: none;
}

.fullscreen .go-left {
  left: 0.5rem;
}

.fullscreen .go-right {
  right: 0.5rem;
}
