/* General layouts for media queries */

.lg-75 {
  padding: 0 1rem;
}

@media (max-width: 768px) {
  .sm-hide {
    display: none;
  }
  .sm-show {
    display: inherit;
  }
  .sm-100 {
    width: 100%;
    margin: 0 auto;
  }
  .md-hide {
    display: inherit;
  }
  .md-show {
    display: none;
  }
}

@media (min-width: 769px) {
  .sm-hide {
    display: inherit;
  }
  .sm-show {
    display: none;
  }
  .md-hide {
    display: none;
  }
  .md-show {
    display: inherit;
  }
  .lg-75 {
    margin: 0 auto;
    max-width: 1200px;
  }
}

@media (max-width: 1024px) {
  .ml-hide {
    display: inherit;
  }

  .ml-show {
    display: none;
  }
}

@media (min-width: 1025px) {
  .ml-hide {
    display: none;
  }
  .ml-show {
    display: inherit;
  }
}

@media (max-width: 1200px) {
  .lg-hide {
    display: inherit;
  }

  .lg-show {
    display: none;
  }

  .lg-75 {
    margin: 0 auto;
    max-width: 1200px;
  }
}

@media (min-width: 1201px) {
  .lg-hide {
    display: none;
  }

  .lg-show {
    display: inherit;
  }

  .lg-75 {
    margin: 0 auto;
    max-width: 1200px;
    width: 75%;
  }
}
