
/**************************************************
 *  FADES
*/

.fade-1s {
    animation: fadeIn ease 1.2s;
    animation-iteration-count: 1;
    animation-fill-mode: forwards;
}

.fade {
    animation: fadeIn ease 0.6s;
    animation-iteration-count: 1;
    animation-fill-mode: forwards;
}

.fade-drop-1 {
    animation: fadeDrop1 ease 3.2s;
    animation-iteration-count: 1;
    animation-fill-mode: forwards;
}

.fade-drop-2 {
    animation: fadeDrop2 ease 3s;
    animation-iteration-count: 1;
    animation-fill-mode: forwards;
}

.fog {
    animation: fog 2.8s;
    background-color: #f3f3f3;
    opacity: 1;
    z-index: 2;
}

@keyframes fog {
    0% {
        background-color: none;
        opacity: 0;
    }
    40% {
        background-color: #f3f3f3;
        opacity: 1;
    }
    55% {
        background-color: #f3f3f3;
        opacity: 1;
    }
    100% {
        background-color: none;
        opacity: 0;
    }
}

@keyframes fadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}


@keyframes fadeDrop1 {
    0% {opacity: 0;}
    40% {opacity: 0;}
    100% {opacity: 1;}
    from {transform: translateY(-4vh);}
    to {transform: translateY(1vh);}
}

@keyframes fadeDrop2 {
    0% {opacity: 0;}
    100% {opacity: 1;}
    to {transform: translateY(1vh);}
}


/**************************************************
 *  CHEVRON MOVEMENT
*/

.chevron {
    animation: chevron-move infinite 5s ease-in-out;
}

@keyframes chevron-move {
    0% {transform: translateY(0rem) scale(1)}
    30% {transform: translateY(0rem) scale(1)}
    45% {transform: translateY(-0.25rem) scale(1.1)}
    60% {transform: translateY(0.25rem) scale(1.1)}
    75% {transform: translateY(-0.25rem) scale(1.1)}
    90% {transform: translateY(0rem) scale(1)}
    100% {transform: translateY(0rem) scale(1)}
}

/**************************************************
 *  GROW
*/

.grow:hover {
    transform: scale(1.5);
    animation: grow infinite 2s ease-in-out;
}

@keyframes grow {
    0% {
        transform:scale(1.00);
    }
    40% {
        transform: scale(1.04);
    }
    90% {
        transform: scale(1.00);
    }
    100% {
        transform:scale(1.00);
    }
}

