#photo-page-container {
  padding-top: 0;
}

.buffer {
  width: 100%;
  height: 8rem;
}

.grid-photos {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 1vw;
  margin: 0 auto;
  padding-bottom: 5rem;
  width: 96vw;
}

.photo-preview {
  display: block;
  height: 31vw;
  object-fit: cover;
  width: 31vw;
}

.photo-preview-container {
  background-color: #f3f3f3;
  height: 31vw;
  width: 31vw;
}

.photo-preview:hover {
  cursor: pointer;
  opacity: 0.94;
}

.photos-intro {
  margin: 1rem 0;
  max-width: 45rem;
}

.photos-page {
  min-height: calc(100vh - 6.5rem);
  padding-top: 4.5rem;
  transition: 0.4s;
}

.photos-main {
  padding: 0 1rem;
  padding-bottom: 1rem;
}

.photos-switch {
  align-items: center;
  display: flex;
  margin-top: 2rem;
}

.dark {
  background-color: #111111;
  color: #f3f3f3;
}

@media (min-width: 769px) {
  .grid-photos {
    width: 91vw;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap: 1vw;
    margin: 0 auto;
  }

  .photo-preview {
    height: 22vw;
    object-fit: cover;
    width: 22vw;
  }

  .photo-preview-container {
    height: 22vw;
    width: 22vw;
  }

  .photos-main {
    margin: 0 2rem;
    padding-bottom: 2rem;
  }
}

@media (min-width: 1201px) {
  .grid-photos {
    width: 79vw;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap: 1vw;
    margin: 0 auto;
  }

  .photo-preview {
    height: 19vw;
    object-fit: cover;
    width: 19vw;
  }

  .photo-preview-container {
    height: 19vw;
    width: 19vw;
  }

  .photos-main {
    margin: 0 auto;
  }
}
