/**************************************************
 * PROJECT LAYOUT
*/

.grid-projects {
  display: block;
}

@media (min-width: 1201px) {
  .grid-projects {
    display: grid;
    grid-template-columns: 1fr 3fr;
  }
}

/**************************************************
 * PROJECT MENU
*/

.project-list {
  border-right: 1px solid #444444;
  height: 100%;
  min-height: calc(100vh - 4.525rem);
  min-width: 14rem;
  max-width: 18rem;
  width: 100%;
}

.project-content {
  padding-left: 0.5rem;
}

.project-item-wrapper {
  text-decoration: none;
  color: #000000;
}

.project-item-wrapper:visited {
  color: #000000;
}

.project-item {
  background-color: #f2f2f2;
  border-radius: 1px;
  line-height: 3rem;
}

.project-item:hover {
  background-color: #e3e3e3;
}

.project-item-after {
  border-bottom: 1px solid #b7b7b7;
  margin: 0 auto;
  width: 95%;
}

/**************************************************
 * PROJECT WELCOME
*/

.surprise {
  position: absolute;
  left: 0;
  width: 99%;
  height: calc(100vh - 5rem);
  z-index: -1;
}

/**************************************************
 * PROJECT SHOW
*/

.button-section {
  margin: 2rem auto 0;
}

.button-section button {
  width: 100%;
  max-width: 25rem;
}

.button-github {
  position: absolute;
  right: -6rem;
  top: 0.25rem;
}

.button-wrapper {
  margin: 0 auto;
  position: relative;
  width: 20rem;
}

.button-container {
  display: flex;
  margin: 0 auto;
  justify-content: center;
  width: 40rem;
}

.button-container button {
  width: 20rem;
  margin: 0.5rem;
}

.description {
  margin: 2rem auto 0;
  max-width: 45rem;
  text-align: left;
  white-space: pre-line;
}

.heading {
  font-size: 2rem;
  margin: 2rem 0 2rem;
  padding-top: 1rem;
  text-align: center;
}

.iframe-section {
  display: flex;
  justify-content: center;
  position: relative;
}

.image-section {
  margin-top: 2rem;
  height: 20rem;
}

.image {
  object-fit: cover;
  width: 100%;
  box-shadow: 0 0 8px 8px white inset;
  height: 20rem;
}

.image-wrapper {
  border-radius: 8%;
  overflow: hidden;
  height: 20rem;
  margin: 0 auto;
  max-width: 30rem;
  position: relative;
  width: 100%;
  box-shadow: 0 0 8px 4px #3b3b3b;
}

.main {
  max-width: 1200px;
  margin: 0 1rem;
  padding-bottom: 1rem;
  position: relative;
}

.selected {
  background-color: #d0d0d0;
  font-weight: 700;
}

.selected:hover {
  background-color: #bbbbbb;
  font-weight: 700;
}

.shadow {
  background-color: #e3e3e3;
  box-shadow: 0 -2rem 10rem 12rem #f3f3f3 inset;
}

@media (max-width: 768px) {
  .button-section {
    width: 100%;
  }

  .button-section button {
    max-width: 20rem;
    width: 100%;
  }

  .button-container {
    margin: 0 auto;
    width: 100%;
    flex-direction: column;
    align-items: center;
  }

  .button-github {
    position: inherit;
    margin: 1rem auto;
    width: 20rem;
  }

  .button-github span {
    font-size: 1.25rem;
    margin-right: 0.75rem;
    line-height: 2.6rem;
  }
}
