

.hamburger-icon {
    background-image: url('../assets/hamburger-menu.svg');
    background-size: contain;
    object-fit: cover;
}

.hamburger-icon:hover {
    cursor: pointer;
    filter: brightness(60%);
}

.hamburger-item:hover {
    background-color: #dadada;
    cursor: pointer;
    opacity: 0.8;
}

.header {
    align-items: center;
    background-color: #450bb6;
    border-bottom: 1px solid #4a4a4a;
    height: 4.5rem;
    display: flex;
    position: fixed;
    z-index: 1;
    top: 0;
    transition: 0.3s;
    width: 100%;
}

.header-hide {
    top: -4.75rem;
}

.logo {
    cursor: pointer;
}

.nav {
    box-sizing: border-box;
    color: #e7e7e7;
    font-size: 1.125rem;
    height: 4.675rem;
    padding: 0.875rem 0 1.125rem;
    text-decoration: none;
}

.nav:hover {
    color: #ffffff;
    border-bottom: 0.1875rem solid #f3f3f3;
}