/**************************************************
 * BUTTON
*/

.button {
  background-color: #1a73e8;
  border: 3px solid #1a73e8;
  border-radius: 0.5rem;
  color: #ffffff;
  display: block;
  font-size: 1.25rem;
  padding: 1rem 0;
  width: 24rem;
}

.button:hover {
  filter: brightness(90%);
  cursor: pointer;
}

.button:active {
  border: 3px solid #4b8add;
  background-color: #5a9bf1;
  filter: brightness(100%);
}

.btn-gray {
  background-color: #acacac;
  border: 3px solid #9d9d9d;
}

.btn-gray:active {
  background-color: #bcbcbc;
  border: 3px solid #adadad;
}

.button-purple {
  background-color: #4e14c2;
  border: 4px solid #4505a5;
  border-radius: 0.5rem;
  color: #ffffff;
  cursor: pointer;
  font-size: 1rem;
  padding: 1rem 2rem;
  width: 100%;
}

.button-purple:hover {
  opacity: 0.82;
}

.button-purple:active {
  background-color: #6432c7;
  border: 4px solid #4a12bb;
  opacity: 0.82;
}

.button-purple:disabled {
  opacity: 1;
  background-color: #380b92;
}

@media (max-width: 480px) {
  .button {
    width: 18rem;
  }
}

/**************************************************
 * ICON LINK
*/

.icon-link:hover {
  opacity: 0.8;
  transform: scale(1.1);
}

/**************************************************
 * DROPDOWN
*/

.dropdown {
  border: 0.125rem solid #888888;
  background-color: #f3f3f3;
  border-radius: 2px;
  font-size: 1.25rem;
  height: 3rem;
  justify-content: center;
  position: relative;
  text-align: center;
  width: 100%;
}

.dropdown:hover {
  cursor: pointer;
}

.dropdown-chevron {
  position: absolute;
  height: 3rem;
  right: 0.5rem;
  top: 0.125rem;
  width: 3rem;
}

.dropdown-item {
  background-color: #f3f3f3;
  height: 3rem;
  line-height: 3rem;
  text-align: center;
  user-select: none;
}

.dropdown-item::after {
  border-bottom: 1px solid #cbcbcb;
  content: "";
  display: block;
  width: 92%;
  position: absolute;
  left: 4%;
}

.dropdown-item:last-child:after {
  content: none;
}

.dropdown-item:hover {
  background-color: #e3e3e3;
  cursor: pointer;
}

.dropdown-options {
  border: 0.125rem solid #888888;
  position: absolute;
  width: 100%;
  top: 3rem;
  left: -0.125rem;
  z-index: 2;
}

@media (max-width: 480px) {
  .dropdown-options {
    max-height: 16.75rem;
    overflow-y: scroll;
  }
}

.dropdown-placeholder {
  color: #7b6b6b;
}

.dropdown-value {
  text-align: center;
  line-height: 3rem;
  width: 100%;
  user-select: none;
}

.hoverable:hover {
  background-color: #e3e3e3;
}

/**************************************************
 * DROPDOWN HIDE CLOSE
*/

.closable {
  display: none;
}

.close {
  display: none;
}

.open {
  display: inherit;
}

/**************************************************
 * COVER to prevent action over specified dom nodes
*/

.covered {
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
}

/**************************************************
 * MAIN CONTAINER responsible for general padding and dimensions of the page
*/

.main-container {
  padding-top: 4.5rem;
  width: 100%;
}

/**************************************************
 * BLUR
*/

.blur {
  filter: blur(2px);
}

/**************************************************
 * ICON classes
*/

.ico-btn {
  background: none;
  border: none;
  cursor: pointer;
  padding: 0;
  max-height: 6rem;
  max-width: 6rem;
}

.ico-full {
  height: 100%;
  width: 100%;
}

.no-drag {
  -webkit-user-drag: none;
  -khtml-user-drag: none;
  -moz-user-drag: none;
  -o-user-drag: none;
}

.no-select {
  user-select: none;
}

/**************************************************
 * HIDE SHOW with transition
*/

.hide-show {
  transition: 2.2s;
  opacity: 1;
}

.hidden {
  opacity: 0;
  visibility: hidden;
}

.flex-jc-center {
  display: flex;
  justify-content: center;
}
