.about-photo {
  margin: 2rem auto;
  width: 265px;
  height: 265px;
  border: 2px solid #8f7676;
  border-radius: 50%;
}

.about-row {
  margin: 0 auto;
  padding-top: 2rem;
}

.about-section {
  display: flex;
  flex-direction: column;
  height: 45rem;
  padding-top: 6rem;
}

.about-subsection {
  align-items: center;
  display: flex;
  width: 100%;
}

.backdrop-1 {
  background-color: bisque;
  bottom: 28%;
  height: 84vh;
  left: -12vw;
  position: absolute;
  top: -14vh;
  transform: rotate(10deg);
  width: 130vw;
  z-index: -1;
}

.card {
  border: 5px solid #b6b6b6;
  border-radius: 2px;
  transition: all 0.3s ease-in-out;
  width: 30rem;
}

.card:hover {
  border: 6px solid #602bca;
  cursor: pointer;
  transform: scale(1.02);
}

.card:active {
  border: 6px solid #602bca;
  transform: scale(1.02);
}

.card-container {
  display: flex;
  justify-content: center;
}

.card-img {
  height: 78%;
  object-fit: cover;
  width: 100%;
}

.featured-button {
  width: 22rem;
}

.grid-featured-projects {
  display: grid;
  grid-gap: 4rem;
  grid-template-columns: 100%;
  grid-template-rows: 4rem 20rem 20rem 20rem 8rem;
  margin-bottom: 4rem;
}

.grid-footer {
  display: grid;
  grid-gap: 1rem;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr 3fr 1fr;
  justify-items: center;
}

.scroll-project-btn {
  height: 6rem;
  width: 6rem;
}

.splash {
  transition: 1s;
  position: static;
}

.splash-hide {
  opacity: 0;
}

.splash-show {
  opacity: 1;
}

.splash-button-inner:hover {
  cursor: pointer;
  background-color: rgba(233, 242, 153, 0.78);
  transition: all 0.4s ease-in-out;
}

.splash-button-inner:active {
  background-color: rgba(255, 255, 255, 0.72);
}

.splash-header {
  left: 5%;
  position: absolute;
  top: 16%;
}

.splash-h1 {
  font-size: 5.5rem;
  padding: 0.5rem 0;
}

@media (min-width: 480px) {
  .featured-button {
    width: 24rem;
  }
}

@media (min-width: 769px) {
  .about-photo {
    margin: 0 auto;
    width: 350px;
    height: 350px;
  }

  .about-section {
    align-content: center;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 4fr 1fr;
    padding-top: 4rem;
    height: 94vh;
  }

  .about-row {
    grid-column: 1/3;
  }

  .card {
    width: 100%;
  }

  .card-img {
    height: 70%;
    object-fit: cover;
    width: 100%;
  }

  .grid-featured-projects {
    display: grid;
    grid-gap: 3rem 1rem;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 4rem 22rem 8rem;
    padding-top: 8rem;
  }

  .splash-header {
    font-size: 6rem;
    left: 12%;
    position: absolute;
    top: 22%;
  }
}
